.table-view-container {
  padding: 1.5rem;
  background: white;
  min-height: 400px;
  font-family: 'Poppins', sans-serif;
  overflow: visible;
}

/* Loading and Error States */
.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 3rem;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #e2e8f0;
  border-top: 4px solid #B68A35;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error-container .error-icon {
  color: #ef4444;
  margin-bottom: 1rem;
}

.error-container h3 {
  color: #374151;
  margin-bottom: 0.5rem;
}

.error-container p {
  color: #6b7280;
  margin-bottom: 1.5rem;
}

/* Table Header */
.table-header [dir='rtl']{
  text-align: right;
}
.table-header {
  margin-bottom: 2rem;
  padding: 1.5rem;
  border-bottom: 1px solid #e5e7eb;
  font-weight: 600;
}

.table-header h2 {
  color: #5F646D;
  font-size: 1.875rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.table-header p {
  color: #9a9a9b;
  font-size: 1.125rem;
}

/* Table Controls */
.table-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 1rem;
  background-color: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.page-size-control {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.page-size-control label {
  color: #374151;
  font-weight: 500;
  font-size: 0.875rem;
}

.page-size-control select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
}

.table-info {
  color: #6b7280;
  font-size: 0.875rem;
}

/* Table Wrapper */
.table-wrapper {
  overflow-x: auto;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  overflow-y: visible;
}

.departments-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  font-size: 0.875rem;
}

.departments-table th,
.departments-table td {
  padding: 1rem;
  text-align: start;
  border-bottom: 1px solid #e5e7eb;
}

.departments-table th {
  background-color: #f9fafb;
  font-weight: 600;
  color: #374151;
  position: sticky;
  top: 0;
  z-index: 10;
}

.departments-table th.sortable {
  cursor: pointer;
  user-select: none;
}

.departments-table th.sortable:hover {
  background-color: #f3f4f6;
}

.th-content {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.sort-icon {
  color: #9ca3af;
  transition: all 0.2s ease;
}

.sort-icon.asc {
  color: #B68A35;
  transform: rotate(180deg);
}

.sort-icon.desc {
  color: #B68A35;
}

/* Table Rows */
.table-row {
  transition: background-color 0.2s ease;
}

.table-row:hover {
  background-color: #f9fafb;
}

.departments-table td {
  color: #374151;
  vertical-align: middle;
}

/* Badge Styles */
.code-badge {
  background-color: #f3f4f6;
  color: #374151;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-weight: 600;
  font-size: 0.75rem;
}

.type-badge {
  background-color: #dbeafe;
  color: #1e40af;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-weight: 500;
  font-size: 0.75rem;
}

.employees-badge {
  background-color: #e0e7ff;
  color: #3730a3;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.75rem;
}

.status-badge {
  padding: 0.375rem 0.75rem;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.status-badge.active {
  background-color: #dcfce7;
  color: #166534;
}

.status-badge.inactive {
  background-color: #fee2e2;
  color: #991b1b;
}

/* Actions Cell */
.actions-cell {
  width: 80px;
  text-align: center;
  overflow: visible;
  position: relative;
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background-color: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.pagination-info {
  color: #6b7280;
  font-size: 0.875rem;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.pagination-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.pagination-btn:hover:not(:disabled) {
  background-color: #f3f4f6;
  border-color: #9ca3af;
}

.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.page-numbers {
  display: flex;
  gap: 0.25rem;
}

.page-btn {
  padding: 0.5rem 0.75rem;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 40px;
}

.page-btn:hover:not(:disabled) {
  background-color: #f3f4f6;
  border-color: #9ca3af;
}

.page-btn.active {
  background-color: #B68A35;
  color: white;
  border-color: #B68A35;
}

.page-btn.ellipsis {
  cursor: default;
  background-color: transparent;
  border: none;
}

.page-btn.ellipsis:hover {
  background-color: transparent;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}

.empty-icon {
  color: #9ca3af;
  margin-bottom: 1rem;
}

.empty-state h3 {
  color: #374151;
  margin-bottom: 0.5rem;
}

.empty-state p {
  color: #6b7280;
}

/* Button Styles */
.btn {
  font-family: 'Poppins', sans-serif;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 0.75rem 1.5rem;
}

/* Ensure English text uses Poppins */
.btn.ltr,
html[dir="ltr"] .btn {
  font-family: 'Poppins', sans-serif;
}

/* Ensure Arabic text uses IBM Plex Sans Arabic */
.btn.rtl,
html[dir="rtl"] .btn {
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

.btn-primary {
  background: linear-gradient(135deg, #B68A35 0%, #B68A35 100%);
  color: white;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #B68A35 0%, #92722A 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(212, 170, 58, 0.3);
}

/* RTL Support */
.table-view-container.rtl .table-controls {
  flex-direction: row-reverse;
}

.table-view-container.rtl .pagination {
  flex-direction: row-reverse;
}

.table-view-container.rtl .pagination-controls {
  flex-direction: row-reverse;
}

.table-view-container.rtl .page-numbers {
  flex-direction: row-reverse;
}

.table-view-container.rtl .th-content {
  flex-direction: row-reverse;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .departments-table th,
  .departments-table td {
    padding: 0.75rem 0.5rem;
  }
  
  .table-controls {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .pagination {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .pagination-controls {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .table-view-container {
    padding: 1rem;
  }
  
  .departments-table th,
  .departments-table td {
    padding: 0.5rem 0.25rem;
    font-size: 0.75rem;
  }
  
  .code-badge,
  .type-badge,
  .employees-badge {
    font-size: 0.625rem;
    padding: 0.125rem 0.25rem;
  }
  
  .status-badge {
    font-size: 0.625rem;
    padding: 0.25rem 0.5rem;
  }
}

@media (max-width: 480px) {
  .departments-table {
    font-size: 0.7rem;
  }
  
  .departments-table th,
  .departments-table td {
    padding: 0.25rem 0.125rem;
  }
  
  .page-btn {
    min-width: 32px;
    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
  }
  .pagination {
    justify-content: center;
    gap: 1rem ;
    display: flex !important;
    flex-direction: column !important;
    align-items: center ;
  }
  .pagination-controls {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
    justify-content: center !important;
  }
}
